.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(0, 0, 0, 0.20);
        backdrop-filter: blur(5px);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #fff;
        width: 1000px;
        max-width: calc(100% - 32px);
        max-height: calc(100 * var(--vh));
        position: relative;
        z-index: 3;
        overflow-y: auto;
        padding: 40px;
        border-radius: 40px;
    }
    &__top{
        padding-right: 32px;
    }
    &__close {
        position: absolute;
        border-top: 0;
        top: 46px;
        right: 40px;
        background-color: transparent;
        width: 28px;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
    &__props{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        &-item{
            margin-top: 8px;
            display: flex;
            align-items: center;
            &:not(:last-child){
                &:after{
                    content: '';
                    flex-shrink: 0;
                    display: block;
                    width: 4px;
                    height: 4px;
                    background-color: rgba($blue,.5);
                    border-radius: 1px;
                    margin: 0 8px !important;
                    transform: rotate(-45deg);
                }
            }
        }
    }
    &__img{
        margin-top: 24px;
        img{
            width: 100%;
            display: block;
            border-radius: 48px;
        }
    }
    .text{
        margin-top: 24px;
    }
}

.req-modal{
    .modal__dialog{
        width: 710px;
        padding: 0;
    }
    .modal__close{
        top: 16px;
        right: 16px;
    }
    &__grid{
        display: grid;
        grid-template-columns: 300px calc(100% - 300px);
        align-items: center;
    }
    &__content{
        padding: 40px;
    }
    &__desc{
        margin-top: 24px;
    }
    &__img{
        min-height: 100%;
        @include imgCover(159%);
    }
    &__contacts{
        position: relative;
    }
}

.req-form{
    margin-top: 16px;
    @include grid100gap(8px);
    &__btn{
        width: 100%;
        margin-top: 32px;
    }
    .m-check{
        margin-top: 8px;
    }
}

.modal-contacts{
    position: absolute;
    bottom: 16px;
    left: 50%;
    transform: translateX(-50%);
    width: calc(100% - 32px);
    border-radius: 24px;
    background: #FFF;
    padding: 24px;
    &__logo{
        width: 152.508px;
        max-width: 100%;
        margin-bottom: 16px;
    }
}

.thank-info{
    text-align: center;
    &__btn{
        margin-top: 40px;
        width: 100%;
    }
    &__icon{
        margin: 40px auto 0;
        border-radius: 50%;
        background: #F5F7F9;
        width: 64px;
        height: 64px;
        @include center;
        svg{
            width: 16px;
            height: auto;
        }
    }
}



@media screen and (max-width: 1031px){
    .modal{
        &__dialog{
            border-radius: 24px;
            padding: 24px 16px;
        }
        &__close{
            top: 24px;
            right: 16px;
        }
        &__top{
            padding-right: 65px;
        }
        &__img{
            img{
                border-radius: 24px;
            }
        }
        .text{
            margin-top: 16px;
        }
    }

    .req-modal{
        &__grid{
            grid-template-columns: 250px calc(100% - 250px);
        }
        &__content{
            padding: 24px;
        }
        &__desc{
            margin-top: 16px;
        }
    }

    .req-form{
        &__btn{
            margin-top: 16px;
        }
    }
}


@media screen and (max-width: 700px){
    .modal{

    }

    .req-modal{
        .modal__dialog{
            background-color: transparent;
        }
        &__grid{
            grid-template-columns: 100%;
        }
        &__content{
            order: -1;
            padding: 24px 16px;
            border-radius: 24px;
            background-color: #fff;
        }
        &__contacts{
            padding: 16px;
        }
        &__img{
            position: absolute;
            top: 0;
            left: 0;
            padding: 0;
            width: 100%;
            height: 100%;
            img{
                position: static;
                border-radius: 24px;
            }
        }
        .text32{
            font-size: 20px;
        }
    }

    .modal-contacts{
        width: 100%;
        left: unset;
        bottom: unset;
        position: relative;
        z-index: 2;
        transform: unset;
    }
}
