.nativejs-select{
  width: 100%;
  margin: 0;
}

.fg {
  position: relative;
  margin: 0;
  input {
    border: none;
    height: 56px;
    padding: 24px 16px 8px;
    width: 100%;
    font-size: 16px;
    line-height: 150%;
    border-radius: 8px;
    background: #F5F7F9;
    color: $blue;
    &._filled,
    &:focus {
      &~label{
        font-size: 12px;
        line-height: 133%;
        top: 8px;
      }
    }
  }
  label{
    font-size: 16px;
    line-height: 150%;
    position: absolute;
    left: 16px;
    top: 16px;
    transition: all .3s;
    color: $gray;
  }
  &__error{
    position: absolute;
    font-size: 12px;
    line-height: 133%;
    right: 16px;
    top: 8px;
    color: #EE0808;
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:hover+label {
      &::before {
        background-color: transparent;
        border-color: $primary;
      }
    }
    &:checked+label {
      &::before {
        background-image: url(../img/check.svg);
        background-color: $primary;
        border-color: $primary;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 24px;
    position: relative;
    min-height: 16px;
    font-size: 14px;
    line-height: 129%;
    cursor: pointer;
    display: block;
    color: $gray;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 4px;
      border: 1px solid rgba(167, 169, 172, 0.25);
      background-color: #EDEEEF;
      @include bgImgSize(9px);
      width: 16px;
      height: 16px;
      left: 0;
      top: 0;
      transition: all .100s;
    }
  }
  a{
    color: $blue;
  }
}
