.news{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 10px));
    gap: 40px 20px;
  }
}

.news-item{
  &__date{
    display: inline-flex;
    align-items: center;
    height: 34px;
    padding: 0 16px;
    border-radius: 8px;
    background: #F5F7F9;
    font-size: 14px;
    line-height: 129%;
    font-weight: 500;
    color: $gray;
  }
}

.article{
  margin-top: 16px;
  color: $dark;
  &__img{
    margin-top: 40px;
    img{
      width: 100%;
      display: block;
      border-radius: 64px;
    }
  }
  .md_show{
    margin-top: 100px;
  }
}

.md_show{
  font-size: 16px;
  line-height: 150%;
  max-width: 745px;
  h2,h3{
    font-weight: 700;
    font-family: $font3;
    color: $blue;
    margin-bottom: 40px;
  }
  h4{
    font-weight: 500;
    color: $blue;
  }
  hr{
    opacity: 0;
    height: 40px;
    display: block;
  }
}


@media screen and (max-width: 1031px){
  .article{
    margin-top: 16px;
    color: $dark;
    &__img{
      margin-top: 24px;
      img{
        border-radius: 24px;
      }
    }
    .md_show{
      margin-top: 40px;
    }
  }

  .md_show{
    h2,h3{
      margin-bottom: 24px;
    }
    hr{
      height: 24px;
    }
  }
}


@media screen and (max-width: 700px){
  .news{
    &__grid{
      grid-template-columns: 100%;
      gap: 24px;
    }
  }

  .article{
    &__img{
      @include imgCover(111%);
    }
  }
}
