.flat-item{
  &__grid{
    margin-top: 40px;
    display: grid;
    grid-template-columns: calc(100% - 340px) 320px;
    gap: 20px;
  }
  &__img{
    @include imgCover(84.8%);
    img{
      border-radius: 48px;
    }
  }
  .tabs{
    margin-top: 16px;
    justify-content: center;
  }
  &__info{
    margin-top: 16px;
    display: flex;
    align-items: center;
  }
  &__price{
    margin-right: 16px;
    font-size: 28px;
    line-height: 114%;
    font-weight: 500;
  }
  &__nav{
    margin-top: 24px;
  }
  &__btn{
    width: 100%;
    height: 48px;
  }
}

.tabs{
  display: flex;
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.tab{
  margin-right: 8px;
  padding: 0 16px;
  height: 40px;
  @include center;
  border-radius: 56px;
  font-weight: 500;
  font-size: 16px;
  transition: all .3s;
  cursor: pointer;
  &:hover,
  &._active{
    background-color: $blue;
    color: #fff;
  }
  &._active{
    pointer-events: none;
  }
}

.props{
  @include grid100gap(8px);
}

.prop-item{
  display: flex;
  justify-content: space-between;
  padding: 4px 12px;
  border-radius: 8px;
  color: $dark;
  &:nth-child(even){
    background: #F5F7F9;
  }
  &__title,
  &__desc{
    font-size: 16px;
    line-height: 150%;
  }
  &__title{
    font-weight: 500;
    margin-right: 10px;
  }
}

.otdelka{
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid #F5F7F9;
  display: flex;
  align-items: center;
  img{
    width: 28px;
    flex-shrink: 0;
    margin-right: 16px;
  }
}

.mortgage-calc{
  .filter{
    margin-top: 40px;
  }
  &__bottom{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  &__result{
    display: flex;
    &-item{
      margin-right: 24px;
    }
  }
}

.mortgages{
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 40px / 3));
    gap: 20px;
  }
}

.mortgage-card{
  padding: 24px;
  border-radius: 24px;
  background: #F5F7F9;
  &__inner{
    @include grid100gap(24px);
  }
  &__img{
    height: 48px;
    display: flex;
    align-items: center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__desc{
    min-height: 66px;
    font-size: 16px;
    line-height: 150%;
    color: $dark;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 64px / 3));
    gap: 32px;
  }
  &__item{
    text-align: center;
    position: relative;
    &:not(:last-child){
      &:after{
        content: '';
        position: absolute;
        left: 100%;
        top: 50%;
        margin-left: 16px;
        transform: translate(-50%,-50%);
        opacity: 0.1;
        background: #1D3670;
        width: 1px;
        height: 24px;
      }
    }
  }
  &__nav{
    display: flex;
    .arrow-link{
      color: $primary;
      &:hover{
        color: $blue;
      }
    }
  }
}


@media screen and (max-width: 1031px){
  .flat-item{
    &__title{
      text-align: center;
    }
    &__grid{
      margin-top: 24px;
      grid-template-columns: 100%;
      gap: 24px;
    }
    &__col{
      &:nth-child(2){
        padding-top: 24px;
        border-top: 1px solid #F5F7F9;
      }
    }
    &__img{
      padding-bottom: 60%;
      img{
        border-radius: 24px;
      }
    }
    &__info{
      flex-direction: column;
      text-align: center;
    }
    &__price{
      margin: 0;
    }
  }

  .mortgages{
    .section__top{
      margin-bottom: 24px;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 10px));
    }
  }
}


@media screen and (max-width: 700px){
  .mortgage-calc{
    &__bottom{
      display: block;
    }
    &__result{
      display: block;
      &-item{
        margin: 0 0 24px;
      }
    }
    &__nav{
      margin-top: 40px;
    }
    &__btn{
      width: 100%;
    }
  }

  .mortgages{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .flat-item{
    .tabs{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 4px));
      gap: 8px;
    }
    .tab{
      width: 100%;
      margin: 0;
    }
    &__img{
      padding-bottom: 111%;
    }
  }

  .tab{
    background: #F5F7F9;
  }
}
