.main{
  margin-top: -64px;
  .swiper{
    &-slide{
      position: relative;
    }
    &-pagination{
      bottom: 16px;
      &-bullet{
        background: #FFF;
        opacity: .5;
        border-radius: 8px;
        width: 24px;
        height: 3px;
        margin: 0 4px !important;
        position: relative;
        &:before{
          content: '';
          @include centerAbsolute;
          height: 400%;
          width: 100%;
        }
        &:hover,
        &-active{
          opacity: 1;
        }
      }
    }
  }
  .text-block{
    margin-top: 64px;
    padding-top: 100px;
    border-top: 1px solid rgba(#A7A9AC,.5);
  }
}

.main-slider{
  color: #fff;
  position: relative;
  &__img{
    border-radius: 0 0 64px 64px;
    @include imgCover(41.7%);
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: inherit;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, rgba(0, 0, 0, 0.30) 100%);
      z-index: 2;
    }
    img{
      border-radius: inherit;
    }
  }
  &__content{
    position: absolute;
    left: 0;
    bottom: 32px;
    width: 100%;
    z-index: 2;
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.main-info{
  margin-top: 104px;
  .text68{
    color: $primary;
  }
  &__items{
    display: flex;
    margin-top: 24px;
  }
  &__item{
    margin-right: 40px;
  }
}

.main-props{
  margin-top: 64px;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 15px));
  gap: 20px;
  text-align: center;
  &__item{
    border-radius: 48px;
    background: #F4F7F9;
    padding: 32px 16px;
  }
  .text32{
    font-family: $font2;
    color: $primary;
    font-weight: 700;
    margin-bottom: 8px;
  }
}

.main-flats,
.advantages-info{
  .section__top{
    margin: 0;
    align-items: flex-start;
  }
  .section__nav{
    width: auto;
  }
}

.main-flats{
  .filter{
    margin-top: 40px;
  }
  &__grid{
    margin-top: 64px;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 12px));
    gap: 16px;
  }
}

.flat-card2{
  border-radius: 16px;
  border: 1px solid $primary;
  padding: 0 16px;
  display: grid;
  align-items: center;
  grid-template-columns: 96px calc(100% - 112px);
  gap: 16px;
  min-height: 80px;
  color: $primary;
  &:hover{
    border-color: $blue;
    color: $blue;
  }
  &:before{
    left: unset;
    right: -7.5px;
    bottom: -27px;
  }
  &__img{
    img{
      width: 100%;
    }
  }
}

.main-mortgage{
  margin-top: 40px;
  &__tabs{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      bottom: 24px;
      left: 0;
      width: 100%;
      height: 1px;
      background: rgba($gray,.5);
    }
    &-wrap{
      padding-bottom: 24px;
      display: flex;
      position: relative;
      z-index: 2;
    }
  }
  .mortgages__grid{
    margin: 0;
  }
}

.tab2{
  padding: 0 8px;
  height: 52px;
  font-size: 20px;
  line-height: 140%;
  color: $dark;
  border: 1px solid $primary;
  margin-right: 16px;
  transition: all .3s;
  flex-shrink: 0;
  &:hover{
    color: $primary;
    cursor: pointer;
  }
  &._active{
    pointer-events: none;
    color: $primary;
    border-top: transparent;
    border-left: transparent;
    border-right: transparent;
  }
}

.offices{
  &__map{
    margin-top: 40px;
    position: relative;
  }
}

.place-card{
  font-family: $font1;
  color: $blue;
  @include grid100gap(8px);
  &__time,
  &__office{
    @include grid100gap(4px);
  }
  &__address{
    margin-top: 8px;
  }
}

.contact-card{
  position: absolute;
  top: 24px;
  left: 24px;
  background-color: #fff;
  border-radius: 24px;
  padding: 24px;
  width: 288px;
  &__img{
    @include imgCover(67%);
    img{
      border-radius: 24px;
    }
  }
  &__content{
    margin-top: 16px;
    @include grid100gap(8px);
  }
  &__address{
    margin-top: 8px;
  }
  &__nav{
    margin-top: 16px;
  }
  &__btn{
    width: 100%;
    height: 43px;
  }
}


@media screen and (max-width: 1031px){
  .main{
    margin-top: -40px;
    .swiper{
      &-pagination{
        display: none;
      }
    }
    .text-block{
      margin-top: 40px;
      padding-top: 40px;
    }
  }

  .main-slider{
    &__img{
      border-radius: 0 0 24px 24px;
    }
  }

  .main-info{
    margin-top: 40px;
  }

  .main-props{
    margin-top: 24px;
    grid-template-columns: repeat(3,calc(100% / 3 - 8px));
    gap: 12px;
    &__item{
      border-radius: 24px;
    }
    .text32{
      font-size: 32px;
    }
  }

  .main-flats,
  .advantages-info{
    .section__nav{
      justify-content: center;
    }
  }

  .main-flats{
    .filter{
      margin-top: 24px;
    }
    &__grid{
      margin-top: 40px;
      grid-template-columns: repeat(3,calc(100% / 3 - 32px / 3));
    }
  }

  .main-mortgage{
    &__tabs{
      &-wrap{
        @include fullScroll;
      }
    }
  }

  .contact-card{
    top: 4px;
    left: 4px;
    width: 280px;
  }
}


@media screen and (max-width: 850px){
  .main-flats{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 8px));
    }
  }
}


@media screen and (max-width: 700px){
  .main{
    margin-top: 0;
  }

  .main-slider{
    width: calc(100% - 32px);
    margin: 0 auto;
    &__img{
      padding-bottom: 139%;
      border-radius: 24px;
    }
    &__content{
      bottom: 16px;
    }
    &__inner{
      flex-direction: column;
    }
    &__btn{
      width: 100%;
      margin: 16px 0 0;
    }
    .text28{
      font-size: 16px;
    }
  }

  .main-info{
    &__items{
      margin-top: 32px;
      display: block;
    }
    &__item{
      margin: 24px 0 0;
    }
  }

  .main-props{
    display: flex;
    @include fullScroll;
    &__item{
      flex-shrink: 0;
      padding: 24px;
      max-width: calc(100vw - 32px);
    }
  }
}


@media screen and (max-width: 576px){
  .main-flats{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .contact-card{
    top: unset;
    bottom: 4px;
    width: calc(100% - 8px);
  }
}
