.docs{
  &__items{
    margin-top: 100px;
    @include grid100gap(8px);
  }
}

.doc-item{
  display: grid;
  grid-template-columns: 40px calc(100% - 116px) 44px;
  gap: 8px 16px;
  align-items: center;
  padding: 20px 40px;
  border-radius: 24px;
  background: #F5F7F9;
  &__btn{
    background: #FFF;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    @include center;
    svg{
      width: 20px;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
}


@media screen and (max-width: 1031px){
  .docs{
    &__items{
      margin-top: 40px;
    }
  }
}


@media screen and (max-width: 700px){
  .doc-item{
    padding: 24px;
    grid-template-columns: 100%;
    &__nav{
      margin-top: 32px;
    }
    &__btn{
      margin-left: auto;
    }
  }
}
