.plan{
  margin-top: 40px;
  position: relative;
  &__wrap,
  &__inner{
    position: relative;
  }
  &__img{
    display: block;
    border-radius: 64px;
    width: 100%;
  }
  &__lighting{
    position: absolute;
    width: 100%;
    height: auto;
    left: 0;
    top: 0;
    path._active{
      opacity: .5 !important;
    }
  }
}

.plan-point{
  position: absolute;
  width: 32px;
  height: 32px;
  border-radius: 8px;
  background-color: $primary;
  color: #fff;
  z-index: 3;
  font-size: 16px;
  line-height: 150%;
  font-weight: 500;
  @include center;
  cursor: pointer;
  &:hover{
    &:before{
      opacity: 1;
    }
  }
  &:before{
    content: '';
    position: absolute;
    left: 100%;
    top: 50%;
    transform: translate(-4px,-50%);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.50);
    border: 1px solid rgba(255, 255, 255, 0.50);
    opacity: 0;
    transition: all .3s;
    pointer-events: none;
  }
  svg{
    width: 18px;
    height: auto;
  }
  &._point1-1{
    top: 24.4%;
    left: 28%;
  }
  &._point1-2{
    top: 30%;
    left: 33.7%;
  }
  &._point2{
    top: 36.3%;
    left: 45.9%;
  }
  &._point3{
    top: 52.9%;
    left: 63%;
  }
  &._point4{
    top: 75.58%;
    left: 80.8%;
  }
  &._point5{
    top: 21.9%;
    left: 41.6%;
  }
  &._point6{
    top: 70.8%;
    left: 44.8%;
  }
  &._point7{
    top: 58.8%;
    left: 35.2%;
  }
  &._point8{
    top: 48%;
    left: 27.7%;
  }
  &._point9{
    top: 42%;
    left: 22.7%;
  }
  &._point10{
    top: 34.2%;
    left: 17.3%;
  }
  &._point11{
    top: 27.5%;
    left: 13.4%;
  }
  &._point12{
    top: 15.5%;
    left: 37.1%;
  }
  &._point13{
    top: 3.8%;
    left: 40.5%;
  }
  &._point14{
    top: 33.3%;
    left: 60.7%;
  }
  &._point-school{
    top: 21.9%;
    left: 18.1%;
    &:hover{
      .plan-point__tooltip{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__tooltip{
    position: absolute;
    top: 99%;
    left: 50%;
    transform: translateX(-50%);
    padding-top: 5px;
    opacity: 0;
    pointer-events: none;
    &-inner{
      border-radius: 40px;
      padding: 8px 16px;
      width: 176px;
      font-weight: 400;
      background-color: #fff;
      color: $blue;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        left: 50%;
        bottom: 100%;
        transform: translateX(-50%);
        background-image: url(../img/triangle.svg);
        @include bgImgSize(100%);
        width: 10px;
        height: 5px;
      }
    }
  }
}

.plan-modal{
  position: absolute;
  z-index: 4;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  height: 100%;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__bg{
    border-radius: 64px;
    background: rgba(#000,.3);
    left: 0;
    top: 0;
    position: absolute;
    width: 100%;
    height: 100%;
  }
  &__wrap{
    width: 500px;
    max-width: 100%;
    padding: 24px 40px 40px;
    border-radius: 24px 24px 0px 0px;
    background: #FFF;
    position: relative;
    z-index: 2;
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__close{
    width: 28px;
    margin-left: 10px;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__items{
    margin-top: 40px;
    @include grid100gap(6px);
  }
  &__bottom{
    margin-top: 48px;
  }
  &__btn{
    width: 100%;
    height: 48px;
  }
}

.room{
  border-radius: 12px;
  background: #F5F7F9;
  padding: 4px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__right{
    display: flex;
    align-items: center;
  }
  &__btn{
    width: 16px;
    flex-shrink: 0;
    margin-left: 16px;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 576px){
  .plan{
    width: calc(100% + 32px);
    margin-left: -16px;
    overflow: hidden;
    &__wrap{
      border-radius: 24px;
      overflow-x: auto;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
    &__inner{
      width: 735px;
    }
    &__img{
      border-radius: 24px;
    }
  }

  .plan-modal{
    padding: 4px;
    &__wrap{
      padding: 24px;
      border-radius: 24px;
    }
    &__bg{
      border-radius: 24px;
    }
    &__bottom{
      margin-top: 40px;
    }
  }
}
