.flats-page{
  .page-title{
    margin-bottom: 16px;
  }
  &__links{
    display: flex;
    justify-content: center;
    &-wrap{
      display: flex;
      align-items: center;
      padding: 8px;
      border-radius: 64px;
      background-color: $blue;
    }
  }
  &__link{
    display: flex;
    align-items: center;
    height: 40px;
    padding: 0 16px;
    font-size: 16px;
    line-height: 150%;
    font-weight: 500;
    color: #fff;
    border-radius: 64px;
    @include lastM(0 8px 0 0);
    &:hover,
    &._active{
      background-color: #fff;
      color: $blue;
    }
  }
  .filter{
    margin-top: 64px;
    padding-top: 64px;
    border-top: 1px solid rgba(#A7A9AC,.5);
  }
  &__title{
    margin-top: 100px;
  }
  &__items{
    margin-top: 40px;
    border-radius: 48px;
    background: #F5F7F9;
    padding: 40px;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 80px / 3));
    gap: 40px;
    .flat-card{
      box-shadow: 0px 4px 28px 0px rgba(29, 54, 112, 0.07);
    }
  }
}

.filter{
  display: grid;
  grid-template-columns: 335px 335px calc(100% - 710px);
  gap: 20px;
  &__buttons{
    margin-top: 40px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__reset{
    display: flex;
    align-items: center;
    font-weight: 500;
    color: $blue;
    margin-right: 40px;
    span{
      position: relative;
      z-index: 2;
    }
    svg{
      margin-left: 12px;
      width: 12px;
      height: auto;
    }
  }
}

.filter-item{
  &__label{
    font-size: 14px;
    line-height: 128%;
    color: $gray;
    margin-bottom: 4px;
  }
  .nativejs-select{
    border: none;
    border-radius: 0;
    &__dropdown{
      border-radius: 8px;
      background: #F5F7F9;
      border: none;
      padding: 4px 22.5px;
    }
    &__option{
      border-radius: 12px;
      background: #FFF;
      height: 32px;
      display: flex;
      align-items: center;
      color: $dark;
      font-style: normal;
      border: none;
    }
  }
  .nativejs-select_active{
    .nativejs-select__placeholder:before{
      transform: translateX(2px) rotate(-180deg);
    }
  }
  .nativejs-select__placeholder{
    font-size: 16px;
    line-height: 150%;
    color: $blue;
    &:before{
      content: '';
      order: 2;
      width: 16px;
      height: 16px;
      flex-shrink: 0;
      margin-left: auto;
      transition: all .3s;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
    }
    &:after{
      display: none;
    }
  }
}

.nativejs-select__placeholder,
.filter-checks,
.range-input,
.filter-field input{
  padding: 0 16px;
  border-radius: 8px;
  background: #F5F7F9;
  height: 56px;
  width: 100%;
}

.filter-field input{
  border: none;
  outline: none;
}

.range-input{
  display: grid;
  grid-template-columns: calc(50% - 24.5px) 1px calc(50% - 24.5px);
  gap: 24px;
  align-items: center;
  &__split{
    height: 12px;
    width: 1px;
    background-color: rgba($gray,.5);
  }
  &__item{
    display: grid;
    grid-template-columns: 26px calc(100% - 26px);
    align-items: center;
    color: $gray;
    font-size: 16px;
    line-height: 150%;
    input{
      font-size: 16px;
      line-height: 150%;
      color: $blue;
      width: 100%;
      height: 100%;
      border: none;
      background-color: transparent;
    }
  }
}

.filter-checks{
  display: flex;
  align-items: center;
  &._advantages{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 4px));
    gap: 8px;
    .filter-checks__item{
      margin: 0 !important;
      label{
        width: 100%;
      }
    }
  }
  &__item{
    position: relative;
    @include lastM(0 9px 0 0);
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      cursor: pointer;
      opacity: 0;
      &:hover+label,
      &:checked+label{
        background-color: $blue;
        color: #fff;
      }
    }
    label{
      transition: all .3s;
      border-radius: 8px;
      height: 40px;
      @include center;
      padding: 0 14px;
      font-size: 16px;
      line-height: 150%;
      font-weight: 500;
    }
  }
}

.building-item{
  .nativejs-select{
    &__options{
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      width: calc(100% + 4px);
      margin-top: -4px;
    }
    &__option{
      width: calc(100% / 6 - 4px);
      padding: 0 10px;
      justify-content: center;
      margin: 4px 4px 0 0;
      &:hover,
      &[data-selected="true"]{
        background: $blue;
        color: #fff;
        border: none;
      }
    }
  }
}

.filter2{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 64px;
}

.sort{
  display: flex;
  align-items: center;
  .filter-item__label{
    margin: 0 24px 0 0;
  }
}

.sort-item{
  margin-right: 24px;
  display: flex;
  align-items: center;
  &._asc{
    .sort-item__line{
      background: $primary;
    }
    ._first{
      width: 100%;
    }
    ._third{
      width: 4px;
    }
  }
  &._desc{
    .sort-item__line{
      background: $primary;
    }
  }
  &__lines{
    width: 14px;
    height: 14px;
    flex-shrink: 0;
    margin-left: 5px;
    padding: 2px 1px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__line{
    width: 12px;
    height: 2px;
    border-radius: 1px;
    background: $blue;
    cursor: pointer;
    transition: all .3s;
    &._first{
      width: 4px;
    }
    &._second{
      width: 7px;
    }
  }
}

.view{
  display: flex;
  align-items: center;
  &__btn{
    display: flex;
    align-items: center;
    margin-left: 24px;
    font-size: 16px;
    line-height: 150%;
    color: $blue;
    font-weight: 500;
    svg{
      width: 14px;
      height: auto;
      margin-right: 8px;
    }
    &--card{
      rect{
        fill: $primary;
      }
    }
  }
}

.flats-catalog._table-view{
  @media screen and (min-width: 1032px){
    .view__btn--card{
      rect{
        fill: $blue;
      }
    }
    .view__btn--table{
      rect{
        fill: $primary;
      }
    }
    .flats-page__items{
      grid-template-columns: 100%;
      gap: 16px;
    }
    .flat-card{
      padding: 16px;
      display: grid;
      grid-template-columns: 100px 204px calc(100% - 552px) 200px;
      gap: 16px;
      align-items: center;
      position: relative;
      .badge{
        top: -4px;
        left: -4px;
        height: 26px;
        padding: 0 10px;
        .text16{
          font-size: 14px;
          line-height: 120%;
        }
      }
      &__img{
        padding-bottom: 100%;
        img{
          height: auto;
          object-fit: unset;
          top: 50%;
          transform: translateY(-50%);
        }
      }
      &__title{
        margin: 0;
        grid-column: 2/3;
        grid-row: 1/2;
      }
      &__props{
        margin: 0;
      }
      &__prices{
        margin: 0;
        text-align: right;
      }
      &__nav{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        opacity: 0;
      }
      &__btn{
        width: 100%;
        height: 100%;
      }
    }
  }
}

.buy-way{
  .section__nav{
    width: auto;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 10px));
    gap: 20px;
  }
}

.buy-card{
  border-radius: 48px;
  background: #F5F7F9;
  padding: 40px;
  display: grid;
  grid-template-columns: calc(100% - 70px) 64px;
  gap: 6px;
  &__content{
    @include grid100gap(8px);
  }
  &__num{
    margin-bottom: 8px;
  }
  &__btn{
    width: 64px;
    height: 64px;
    @include center;
    background-color: #fff;
    border-radius: 50%;
    svg{
      width: 24px;
      height: auto;
    }
  }
}



@media screen and (max-width: 1031px){
  .flats-page{
    .page-title{
      margin-bottom: 24px;
    }
    .filter{
      margin-top: 40px;
      padding-top: 40px;
    }
    &__title{
      margin-top: 40px;
      padding-top: 40px;
      border-top: 1px solid rgba(#A7A9AC,.5);
    }
    &__items{
      padding: 20px;
      width: calc(100% + 32px);
      margin-left: -16px;
      border-radius: 24px;
      grid-template-columns: repeat(2,calc(50% - 8px));
      gap: 16px;
    }
  }

  .filter{
    grid-template-columns: repeat(2,calc(50% - 8px));
    gap: 16px;
  }

  .filter2{
    margin-top: 24px;
  }

  .view{
    display: none;
  }

  .buy-way{
    .section__top{
      margin-bottom: 16px;
    }
    .section__nav{
      justify-content: center;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - 8px));
      gap: 16px;
    }
  }

  .buy-card{
    border-radius: 24px;
    padding: 24px;
    grid-template-columns: 100%;
    gap: 24px;
    &__nav{
      order: -1;
    }
    &__btn{
      margin-left: auto;
    }
  }
}


@media screen and (max-width: 700px){
  .filter{
    grid-template-columns: 100%;
    &__buttons{
      flex-direction: column;
    }
    &__submit{
      order: -1;
      width: 100%;
    }
    &__reset{
      margin: 32px 0 0;
    }
  }
}


@media screen and (max-width: 576px){
  .flats-page{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .buy-way{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .sort{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 8px));
    gap: 24px 16px;
    .filter-item__label{
      margin: 0;
      grid-column: 1/-1;
    }
  }

  .sort-item{
    margin: 0;
    width: 100%;
    border-radius: 56px;
    background: #F5F7F9;
    justify-content: center;
    height: 40px;
  }
}
