@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    max-width: 100%;
    width: 240px;
    height: 56px;
    font-size: 16px;
    line-height: 150%;
    font-weight: 500;
    border-radius: 1000px;
    &:focus {
        box-shadow: none;
    }
    &._small{
        width: auto;
        height: 39px;
        padding: 0 20px;
    }
    span{
        font-size: 16px;
        line-height: 150%;
    }
}

.m-btn-white{
    background-color: #fff;
    color: $blue;
    &:hover{
        background-color: $primary;
        color: #fff;
    }
}

.m-btn-blue{
    background-color: $blue;
    color: #fff;
    &:hover{
        background-color: $primary;
        color: #fff;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: #F58B1C;
        color: #fff;
    }
    &:disabled{
        background: rgba(238, 119, 8, 0.20);
        pointer-events: none;
    }
}

.m-btn-primary-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: #fff;
    }
}

.m-btn-blue-outline{
    border: 1px solid $blue;
    color: $blue;
    &:hover{
        background-color: $blue;
        color: #fff;
    }
}

.swiper-button,
.round-btn{
    width: 32px;
    height: 32px;
    border-radius: 50%;
}

.primary-link{
    color: $primary;
    &:hover{
        color: #F58B1C;
    }
}

.arrow-link{
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 150%;
    font-weight: 500;
    &:hover{
        color: $primary;
        path{
            fill: $primary;
        }
    }
    svg{
        width: 16px;
        height: auto;
        margin: 0 8px;
    }
}

.pagination__btn,
.pagination__item,
._round-hover{
    position: relative;
    &:before{
        content: '';
        position: absolute;
        left: 8px;
        bottom: -23px;
        width: 43px;
        height: 43px;
        background: rgba(#000,.1);
        border-radius: 50%;
        transition: inherit;
        opacity: 0;
    }
    &:hover{
        color: $primary;
        &:before{
            opacity: 1;
        }
    }
}
