.buy{
  &__inner{
    padding: 64px;
    border-radius: 64px;
    background-color: $lightBlue;
    position: relative;
  }
  &__info{
    max-width: 536px;
    @include grid100gap(24px);
    .text68{
      color: $primary;
    }
  }
  &__nav{
    margin-top: 192px;
    display: flex;
  }
  &__img{
    position: absolute;
    right: 25px;
    bottom: 0;
    width: 484px;
    max-width: 100%;
  }
}


@media screen and (max-width: 1031px){
  .buy{
    .container{
      padding: 0;
    }
    &__inner{
      padding: 24px;
      border-radius: 24px;
    }
    &__info{
      gap: 16px;
    }
    &__img{
      width: 350px;
    }
  }
}


@media screen and (max-width: 700px){
  .buy{
    &__inner{
      padding-bottom: 0;
    }
    &__img{
      width: calc(100% + 28px);
      position: static;
      margin-left: -14px;
      margin-top: 11px;
    }
    &__nav{
      position: absolute;
      bottom: 24px;
      padding: 0 24px;
      left: 0;
      z-index: 2;
      width: 100%;
    }
    &__btn{
      width: 100%;
    }
  }
}
